@import "index", "highlight";

.archive {
  font-size: 1.1em; }

.archive time {
  display: inline-block;
  min-width: 10ch;
  margin: 0 .2em; }



.categories {
  color: $link-color;
  margin-bottom: .15em; }





.title {
  font-size: 1.3em; }





.hidden {
  display: none; }





.icons svg {
  height: 1em;
  width: 1em;
  fill: currentColor;
  transition: .2s color;
  vertical-align: middle;
  margin-bottom: .15em; }







.related div {
  flex: 1 0 15em; }

.related span {
  display: block;
  color: reduce(100); }

.related a {
  font-size: 1.2em;
  font-weight: bold; }

@media (min-width: 30em) {
  .next {
    text-align: right; } }






